// src/pages/Login.js
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Form, Button, Alert } from 'react-bootstrap';
import { useTranslation } from 'react-i18next'; // Import useTranslation
import { useNavigate } from 'react-router-dom'; // Import useNavigate for redirection

const Login = () => {
  const { t, i18n } = useTranslation(); // Initialize translation
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(''); // State for error messages
  const navigate = useNavigate(); // Hook for programmatic navigation

  useEffect(() => {
    // Dynamically set the font based on language
    document.body.className = i18n.language === 'bn' ? 'bengali' : '';
  }, [i18n.language]); // Run the effect when the language changes

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      // Send login request to the backend
      const res = await axios.post('/api/users/login', { email, password });
      
      // Check if the login was successful
      if (res.status === 200 && res.data.token) {
        // Save the token in local storage (or another secure place)
        localStorage.setItem('token', res.data.token);
        
        // Redirect to the Admin page
        navigate('/admin'); // Adjust the path as needed
      } else {
        // Handle login failure
        setError(t('loginFailed')); // Show error message if login fails
      }
    } catch (err) {
      // Handle request error
      console.error(err);
      setError(err.response?.data?.message || t('loginError')); // Show error message
    }
  };

  return (
    <div className="container mt-5 login-container">
      <h2 className='mt-5 mb-2'>{t('login')}</h2> {/* Translate Login title */}
      {error && <Alert variant="danger">{error}</Alert>} {/* Show error messages */}
      <Form onSubmit={handleSubmit}>
        <Form.Group controlId="formEmail">
          <Form.Label>{t('email')}</Form.Label> {/* Translate Email label */}
          <Form.Control 
            type="email" 
            value={email} 
            onChange={(e) => setEmail(e.target.value)} 
            placeholder={t('enterEmail')} // Optional placeholder
          />
        </Form.Group>
        <Form.Group controlId="formPassword">
          <Form.Label>{t('password')}</Form.Label> {/* Translate Password label */}
          <Form.Control 
            type="password" 
            value={password} 
            onChange={(e) => setPassword(e.target.value)} 
            placeholder={t('enterPassword')} // Optional placeholder
          />
        </Form.Group>
        <Button variant="primary" type="submit">{t('login')}</Button> {/* Translate Login button */}
      </Form>
    </div>
  );
};

export default Login;
