// src/components/Register.js
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

const Register = () => {
  const { t, i18n } = useTranslation();
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    password: '',
    confirmPassword: '',
  });
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    document.body.className = i18n.language === 'bn' ? 'bengali' : '';
  }, [i18n.language]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const checkUserExists = async () => {
    try {
      const response = await axios.post('/api/auth/check-user', {
        email: formData.email,
        username: formData.username,
      });
      return response.data.exists;
    } catch (err) {
      console.error('Error checking user existence:', err);
      return false;
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (formData.password !== formData.confirmPassword) {
      setError(t('passwordMismatch'));
      return;
    }

    setLoading(true);
    const userExists = await checkUserExists();

    if (userExists) {
      setError(t('userExists')); // Error message for existing user
      setLoading(false);
      return;
    }

    try {
      const { data } = await axios.post('/api/auth/register', formData);
      if (data.success) {
        navigate('/login');
      } else {
        setError(data.message);
      }
    } catch {
      setError(t('registrationFailed'));
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="container register-container">
      <h2 className="mt-5 mb-2">{t('register')}</h2>
      {error && <p className="text-danger">{error}</p>}
      <form onSubmit={handleSubmit}>
        {['username', 'email', 'password', 'confirmPassword'].map((field, idx) => (
          <div className="mb-3" key={idx}>
            <label htmlFor={field} className="form-label">{t(field)}</label>
            <input
              type={field.includes('password') ? 'password' : 'text'}
              className="form-control"
              id={field}
              name={field}
              value={formData[field]}
              onChange={handleChange}
              required
            />
          </div>
        ))}
        <button type="submit" className="btn btn-primary" disabled={loading}>
          {loading ? t('registering') : t('register')}
        </button>
      </form>
    </div>
  );
};

export default Register;
