// src/components/DynamicCustomFields.js
import React, { useState } from 'react';

const DynamicCustomFields = () => {
  const [fields, setFields] = useState([{ id: Date.now(), type: 'text', label: '' }]);

  const handleFieldChange = (id, key, value) => {
    setFields(fields.map(field => field.id === id ? { ...field, [key]: value } : field));
  };

  const handleAddField = () => setFields([...fields, { id: Date.now(), type: 'text', label: '' }]);
  const handleFieldRemove = (id) => setFields(fields.filter(field => field.id !== id));

  return (
    <div>
      <h2>Dynamic Custom Fields</h2>
      {fields.map(({ id, type, label, options }) => (
        <div key={id} style={{ marginBottom: '10px' }}>
          <input
            type="text"
            placeholder="Label"
            value={label}
            onChange={(e) => handleFieldChange(id, 'label', e.target.value)}
          />
          <select
            value={type}
            onChange={(e) => handleFieldChange(id, 'type', e.target.value)}
          >
            {['text', 'number', 'email', 'select'].map((typeOption) => (
              <option key={typeOption} value={typeOption}>{typeOption.charAt(0).toUpperCase() + typeOption.slice(1)}</option>
            ))}
          </select>
          {type === 'select' && (
            <input
              type="text"
              placeholder="Options (comma separated)"
              onChange={(e) => handleFieldChange(id, 'options', e.target.value.split(','))}
            />
          )}
          <button onClick={() => handleFieldRemove(id)}>Remove</button>
        </div>
      ))}
      <button onClick={handleAddField}>Add Custom Field</button>

      <h3>Rendered Fields:</h3>
      {fields.map(({ id, type, label, options }) => (
        <div key={id}>
          <label>{label}:</label>
          {type === 'text' && <input type="text" />}
          {type === 'number' && <input type="number" />}
          {type === 'email' && <input type="email" />}
          {type === 'select' && (
            <select>
              {options?.map((option, index) => (
                <option key={index} value={option}>{option}</option>
              ))}
            </select>
          )}
        </div>
      ))}
    </div>
  );
};

export default DynamicCustomFields;
