// src/pages/AdminPage.js
import React, { useEffect, useState } from 'react';
import { Container, Row, Col, Table, Button, Card, Modal } from 'react-bootstrap';
import axios from 'axios';
import { useTranslation } from 'react-i18next';
import './css/Admin.css';

const AdminPage = () => {
  const { t, i18n } = useTranslation();
  const [users, setUsers] = useState([]);  // State for user data
  const [showModal, setShowModal] = useState(false);  // State for modal visibility
  const [selectedUser, setSelectedUser] = useState(null);  // State for selected user
  const [action, setAction] = useState('');  // State for action type

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        // Fetch user data from backend
        const response = await axios.get('/api/admin/users', {
          headers: { Authorization: `Bearer ${localStorage.getItem('token')}` },
        });
        setUsers(response.data);  // Update users state
      } catch (error) {
        console.error('Error fetching users:', error);
      }
    };
    fetchUsers();
    document.body.className = i18n.language === 'bn' ? 'bengali' : '';
  }, [i18n.language]);  // Dependency on language change

  const handleAction = async (userId, actionType) => {
    try {
      // Perform user action (block, unblock, make admin, etc.)
      const response = await axios.post(`/api/admin/users/${userId}/${actionType}`, {}, {
        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` },
      });
      setUsers(users.map(user => (user.id === userId ? response.data : user)));  // Update users state
    } catch (error) {
      console.error(`Error performing action ${actionType}:`, error);
    }
  };

  const handleDelete = async (userId) => {
    try {
      // Delete user
      await axios.delete(`/api/admin/users/${userId}`, {
        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` },
      });
      setUsers(users.filter(user => user.id !== userId));  // Remove user from state
    } catch (error) {
      console.error('Error deleting user:', error);
    }
  };

  const handleOpenModal = (user, actionType) => {
    setSelectedUser(user);
    setAction(actionType);
    setShowModal(true);  // Show confirmation modal
  };

  const handleConfirmAction = () => {
    if (action === 'delete') {
      handleDelete(selectedUser.id);
    } else {
      handleAction(selectedUser.id, action);
    }
    setShowModal(false);  // Close modal
  };

  return (
    <Container className="admin-page-container">
      <h2>{t('adminPanel')}</h2>
      <Row>
        <Col md={12}>
          <Card className="admin-card">
            <Card.Header>{t('userManagement')}</Card.Header>
            <Card.Body>
              <Table striped bordered hover responsive>
                <thead>
                  <tr>
                    {['ID', 'username', 'email', 'admin', 'blocked', 'actions'].map(header => (
                      <th key={header}>{t(header)}</th>
                    ))}
                  </tr>
                </thead>
                <tbody>
                  {users.map(user => (
                    <tr key={user.id}>
                      <td>{user.id}</td>
                      <td>{user.username}</td>
                      <td>{user.email}</td>
                      <td>{t(user.isAdmin ? 'yes' : 'no')}</td>
                      <td>{t(user.isBlocked ? 'yes' : 'no')}</td>
                      <td>
                        <Button variant="danger" onClick={() => handleOpenModal(user, 'delete')} className="me-2">
                          {t('delete')}
                        </Button>
                        <Button variant={user.isBlocked ? 'success' : 'warning'} onClick={() => handleOpenModal(user, user.isBlocked ? 'unblock' : 'block')} className="me-2">
                          {t(user.isBlocked ? 'unblock' : 'block')}
                        </Button>
                        <Button variant={user.isAdmin ? 'secondary' : 'primary'} onClick={() => handleOpenModal(user, user.isAdmin ? 'remove-admin' : 'make-admin')}>
                          {t(user.isAdmin ? 'removeAdmin' : 'makeAdmin')}
                        </Button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>{t('confirmAction')}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {t('confirmActionMessage', { action, username: selectedUser?.username })}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowModal(false)}>
            {t('cancel')}
          </Button>
          <Button variant="primary" onClick={handleConfirmAction}>
            {t('confirm')}
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
};

export default AdminPage;
