// src/components/CollectionForm.js
import React, { useState } from 'react';
import { Form, Button } from 'react-bootstrap';
import axios from 'axios'; // Ensure axios is imported

const CollectionForm = ({ onSubmit }) => {
  const [formData, setFormData] = useState({ name: '', description: '', category: '' });
  const [selectedFields, setSelectedFields] = useState({ author: false, synopsis: false, publicationYear: false });
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleChange = (e) => setFormData({ ...formData, [e.target.name]: e.target.value });
  const handleCheckboxChange = (e) => setSelectedFields({ ...selectedFields, [e.target.name]: e.target.checked });

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    const customFields = ['author', 'synopsis', 'publicationYear']
      .filter(field => selectedFields[field])
      .map(field => ({
        label: field.charAt(0).toUpperCase() + field.slice(1).replace(/([A-Z])/g, ' $1'),
        type: field === 'publicationYear' ? 'date' : 'string',
        value: ''
      }));

    try {
      await axios.post('/api/collections', { ...formData, customFields });
      if (onSubmit) onSubmit();
    } catch (err) {
      setError('Failed to save collection');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Form onSubmit={handleSubmit}>
      {['name', 'description', 'category'].map((field, idx) => (
        <Form.Group key={idx}>
          <Form.Label>{field.charAt(0).toUpperCase() + field.slice(1)}</Form.Label>
          <Form.Control
            as={field === 'description' ? 'textarea' : 'input'}
            type={field === 'category' ? 'text' : 'text'}
            name={field}
            value={formData[field]}
            onChange={handleChange}
            required={field !== 'description'}
          />
        </Form.Group>
      ))}
      {['author', 'synopsis', 'publicationYear'].map((field, idx) => (
        <Form.Check
          key={idx}
          type="checkbox"
          label={`Include ${field.charAt(0).toUpperCase() + field.slice(1)}`}
          name={field}
          checked={selectedFields[field]}
          onChange={handleCheckboxChange}
        />
      ))}
      {error && <p className="text-danger">{error}</p>}
      <Button type="submit" disabled={loading}>
        {loading ? 'Saving...' : 'Save Collection'}
      </Button>
    </Form>
  );
};

export default CollectionForm;
