// src/pages/ItemPage.js
import React, { useEffect, useState } from 'react';
import { Container, Row, Col, Card, Form, Button, ListGroup, Badge } from 'react-bootstrap';
import axios from 'axios';
import { useTranslation } from 'react-i18next';
import 'bootstrap/dist/css/bootstrap.min.css';
import './css/Items.css';

const ItemPage = () => {
  const { t, i18n } = useTranslation();
  const [items, setItems] = useState([]);  // State for items
  const [comments, setComments] = useState([]);  // State for comments
  const [newComment, setNewComment] = useState('');  // State for new comment input
  const [sortOrder, setSortOrder] = useState('asc');  // State for sort order
  const [filter, setFilter] = useState('');  // State for filter
  const [likes, setLikes] = useState(0);  // State for likes
  const [hasLiked, setHasLiked] = useState(false);  // State for like status
  const itemId = 1; // Replace with dynamic ID based on your routing

  useEffect(() => {
    const fetchData = async () => {
      try {
        // Fetch data for items, comments, and specific item details
        const [itemsRes, commentsRes, itemRes] = await Promise.all([
          axios.get('http://localhost:5000/items'),
          axios.get(`http://localhost:5000/items/${itemId}/comments`),
          axios.get(`http://localhost:5000/items${itemId}`)
        ]);
        setItems(itemsRes.data);  // Update items state
        setComments(commentsRes.data);  // Update comments state
        setLikes(itemRes.data.likes);  // Update likes state
        setHasLiked(itemRes.data.hasLiked);  // Assuming backend sends if user has liked
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
    document.body.className = i18n.language === 'bn' ? 'bengali' : '';

    // Refresh data every 5 seconds
    const interval = setInterval(fetchData, 5000);
    return () => clearInterval(interval);  // Clear interval on component unmount
  }, [itemId, i18n.language]);  // Dependencies for useEffect

  const handleCommentSubmit = async (e) => {
    e.preventDefault();
    if (newComment.trim()) {
      try {
        await axios.post(`/api/items/${itemId}/comments`, { text: newComment });
        setNewComment('');
        // Optionally refetch comments here if needed
      } catch (error) {
        console.error('Error submitting comment:', error);
      }
    }
  };

  const handleLike = async () => {
    if (!hasLiked) {
      try {
        await axios.post(`/api/items/${itemId}/like`);
        setLikes(likes + 1);
        setHasLiked(true);
      } catch (error) {
        console.error('Error liking item:', error);
      }
    }
  };

  // Sort and filter items
  const sortedItems = [...items].sort((a, b) => sortOrder === 'asc' ? a.name.localeCompare(b.name) : b.name.localeCompare(a.name));
  const filteredItems = sortedItems.filter(item => item.name.toLowerCase().includes(filter.toLowerCase()));

  return (
    <Container fluid className="item-page-container">
      <Row className="mt-5">
        <Col>
          <h2 className="section-title">{t('items')}</h2>
          <Form>
            <Row className="mb-3">
              <Col md={6}>
                <Form.Control
                  type="text"
                  placeholder={t('filterPlaceholder')}
                  value={filter}
                  onChange={(e) => setFilter(e.target.value)}
                />
              </Col>
              <Col md={6} className="text-md-end">
                <Form.Select value={sortOrder} onChange={(e) => setSortOrder(e.target.value)}>
                  <option value="asc">{t('sortAsc')}</option>
                  <option value="desc">{t('sortDesc')}</option>
                </Form.Select>
              </Col>
            </Row>
          </Form>
          <Row>
            {filteredItems.map(item => (
              <Col md={4} key={item.id} className="mb-4">
                <Card>
                  <Card.Body>
                    <Card.Title>{item.name}</Card.Title>
                    <Card.Text>{item.description}</Card.Text>
                    <Button variant="primary">{t('viewDetails')}</Button>
                  </Card.Body>
                  <Card.Footer>
                    <Badge pill bg="info" className="me-2">{t('category')}: {item.category}</Badge>
                    <Badge pill bg="secondary">{t('tags')}: {item.tags.join(', ')}</Badge>
                    <Button variant="success" className="ms-2" onClick={handleLike} disabled={hasLiked}>
                      {hasLiked ? t('liked') : t('like')} ({likes})
                    </Button>
                  </Card.Footer>
                </Card>
              </Col>
            ))}
          </Row>
        </Col>
      </Row>

      <Row className="mt-5">
        <Col>
          <h2 className="section-title">{t('comments')}</h2>
          <ListGroup>
            {comments.map((comment, index) => (
              <ListGroup.Item key={index}>{comment.text}</ListGroup.Item>
            ))}
          </ListGroup>
          <Form className="mt-4" onSubmit={handleCommentSubmit}>
            <Form.Group controlId="newComment">
              <Form.Label>{t('leaveComment')}</Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                value={newComment}
                onChange={(e) => setNewComment(e.target.value)}
              />
            </Form.Group>
            <Button variant="primary" type="submit" className="mt-3">{t('submit')}</Button>
          </Form>
        </Col>
      </Row>
    </Container>
  );
};

export default ItemPage;
