// src/pages/HomePage.js
import React, { useEffect, useState } from 'react';
import { Container, Row, Col, Card, Form, Button, InputGroup } from 'react-bootstrap';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import './css/Home.css';

const HomePage = () => {
  const { t, i18n } = useTranslation();
  const [searchQuery, setSearchQuery] = useState('');  // State for search query
  const [searchResults, setSearchResults] = useState([]);  // State for search results
  const [recentItems, setRecentItems] = useState([]);  // State for recent items
  const [topCollections, setTopCollections] = useState([]);  // State for top collections
  const [tags, setTags] = useState([]);  // State for tags

  useEffect(() => {
    const fetchData = async () => {
      try {
        // Fetch recent items, top collections, and tags from the API
        const [recentRes, topRes, tagsRes] = await Promise.all([
          axios.get('http://localhost:5000/items'),
          axios.get('http://localhost:5000/collections'),
          axios.get('http://localhost:5000/tags'),
        ]);
        setRecentItems(recentRes.data);  // Update recent items state
        setTopCollections(topRes.data);  // Update top collections state
        setTags(tagsRes.data);  // Update tags state
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
    document.body.className = i18n.language === 'bn' ? 'bengali' : '';
  }, [i18n.language]);  // Dependency on language change

  const handleSearch = async (e) => {
    e.preventDefault();
    try {
      // Perform search with the query and update search results state
      const response = await axios.get(`/api/search?q=${searchQuery}`);
      setSearchResults(response.data);
    } catch (error) {
      console.error('Error performing search:', error);
    }
  };

  const renderListItems = (items, isRecent = false) => (
    items.map(item => (
      <li key={item.id}>
        <Link to={`/items/${item.id}`}>{item.name}</Link>
        {isRecent ? ` (Collection: ${item.collectionName}, Author: ${item.author})` : ` (Items: ${item.itemCount})`}
      </li>
    ))
  );

  return (
    <Container className="home-page-container">
      <header className="header d-flex justify-content-between align-items-center">
        <h1>{t('welcome')}</h1>
      </header>

      <Form onSubmit={handleSearch} className="search-form">
        <InputGroup>
          <Form.Control
            type="text"
            placeholder={t('searchPlaceholder')}
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
          <Button variant="primary" type="submit" className='search-btn'>{t('Search')}</Button>
        </InputGroup>
      </Form>

      <Row className="mt-4">
        <Col md={4}>
          <Card className="mb-4">
            <Card.Header>{t('recentlyAdded')}</Card.Header>
            <Card.Body>
              <ul>{renderListItems(recentItems, true)}</ul>
            </Card.Body>
          </Card>
        </Col>

        <Col md={4}>
          <Card className="mb-4">
            <Card.Header>{t('topCollections')}</Card.Header>
            <Card.Body>
              <ul>{renderListItems(topCollections)}</ul>
            </Card.Body>
          </Card>
        </Col>

        <Col md={4}>
          <Card className="mb-4">
            <Card.Header>{t('tagCloud')}</Card.Header>
            <Card.Body>
              <div className="tag-cloud">
                {tags.map(tag => (
                  <Link to={`/search?q=${tag.name}`} key={tag.name} className="tag">
                    {tag.name}
                  </Link>
                ))}
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {searchResults.length > 0 && (
        <Row className="mt-4">
          <Col md={12}>
            <Card>
              <Card.Header>{t('searchResults')}</Card.Header>
              <Card.Body>
                <ul>{renderListItems(searchResults)}</ul>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      )}
    </Container>
  );
};

export default HomePage;
