// src/pages/CollectionsPage.js
import React, { useEffect, useState } from 'react';
import { Container, Row, Col, Card, Form, Button, Modal, Badge } from 'react-bootstrap';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';
import './css/Collections.css';
import CollectionForm from '../components/CollectionForm';
import { useTranslation } from 'react-i18next';

const CollectionsPage = () => {
  const { t, i18n } = useTranslation();
  const [collections, setCollections] = useState([]);  // State for collections
  const [sortOrder, setSortOrder] = useState('asc');  // State for sort order
  const [filter, setFilter] = useState('');  // State for filter text
  const [showModal, setShowModal] = useState(false);  // State for modal visibility

  useEffect(() => {
    const fetchData = async () => {
      try {
        // Fetch collections from the backend
        const response = await axios.get('/api/collections');
        setCollections(response.data);  // Update collections state
      } catch (error) {
        console.error('Error fetching collections:', error);
      }
    };
    fetchData();
    document.body.className = i18n.language === 'bn' ? 'bengali' : '';
  }, [i18n.language]);  // Dependency on language change

  const handleCollectionSubmit = async (newCollection) => {
    try {
      // Post new collection to the backend and update state
      const response = await axios.post('/api/collections', newCollection);
      setCollections(prev => [...prev, response.data]);  // Add new collection to state
      setShowModal(false);  // Close modal
    } catch (error) {
      console.error('Error creating collection:', error);
    }
  };

  const sortedCollections = [...collections].sort((a, b) => 
    sortOrder === 'asc' ? a.name.localeCompare(b.name) : b.name.localeCompare(a.name)
  );

  const filteredCollections = sortedCollections.filter(collection =>
    collection.name.toLowerCase().includes(filter.toLowerCase())
  );

  return (
    <Container fluid className="collections-page-container">
      <Row className="mt-5">
        <Col>
          <h2 className="section-title">{t('collectionsTitle')}</h2>
          <Form>
            <Row className="mb-3">
              <Col md={6}>
                <Form.Control
                  type="text"
                  placeholder={t('filterCollectionsPlaceholder')}
                  value={filter}
                  onChange={(e) => setFilter(e.target.value)}
                />
              </Col>
              <Col md={6} className="text-md-end">
                <Form.Select value={sortOrder} onChange={(e) => setSortOrder(e.target.value)}>
                  <option value="asc">{t('sortByNameAZ')}</option>
                  <option value="desc">{t('sortByNameZA')}</option>
                </Form.Select>
              </Col>
            </Row>
          </Form>
          <Button variant="primary" onClick={() => setShowModal(true)}>
            {t('createNewCollection')}
          </Button>
          <Row className="mt-4">
            {filteredCollections.map(collection => (
              <Col md={4} key={collection.id} className="mb-4">
                <Card>
                  <Card.Body>
                    <Card.Title>{collection.name}</Card.Title>
                    <Card.Text>{collection.description}</Card.Text>
                    <Button variant="primary">{t('viewCollection')}</Button>
                  </Card.Body>
                  <Card.Footer>
                    <Badge pill bg="info" className="me-2">{collection.category}</Badge>
                  </Card.Footer>
                </Card>
              </Col>
            ))}
          </Row>
        </Col>
      </Row>

      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>{t('createNewCollection')}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <CollectionForm onSubmit={handleCollectionSubmit} />
        </Modal.Body>
      </Modal>
    </Container>
  );
};

export default CollectionsPage;
